<template>
    <div class="user-label">
        {{ user.username }}
        <button @click="removeUser">X</button>
    </div>
</template>

<script>
export default {
    props: ['user'],
    methods: {
        removeUser() {
            this.$emit('remove-user', this.user);
        }
    }
}
</script>

<style scoped>
.user-label {
    display: inline-block;
    padding: 5px;
    margin: 5px;
    border: 1px solid #ccc;
    border-radius: 5px;
}
</style>
